html {
    display: table;
    margin: auto;
}

body {
    /*font: 14px "Century Gothic", Futura, sans-serif;*/
    display: table-cell;
    vertical-align: middle;

  }
  .content{
    max-width: 500px;
    margin: auto;
  }

  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }
  
  .square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    position: center;
    display: flex;
    flex-direction: row;
  }
  
  .game-info {
    position: center;
    margin-left: 30px;
  }
  